(function() {
    const tagsInput = document.querySelector('#tags_input')

    if(tagsInput) {
        const tagsDiv = document.querySelector('#tags');
        const tagsInputHidden = document.querySelector('[name="tags"]');

        let tags = [];

        //RECUPERAR del input oculto en AREA de EXPERIENCIA
        if(tagsInputHidden.value!== '') {
            tags = tagsInputHidden.value.split(','); //convierte el string en  array (arreglos)
            mostrarTags();
        }

        // Escuchar los cambios en el input
        tagsInput.addEventListener('keypress', guardarTag)

        function guardarTag(e) {
            if(e.keyCode === 44) { // 44 es ,

                if(e.target.value.trim() === '' || e.target.value < 1) {
                    return;
                }   
                e.preventDefault(); //previene el default que borra la ,
                
                tags = [...tags, e.target.value.trim()] //arreglo de etiquetas
                tagsInput.value = '';

                mostrarTags();
            }
        }

        function mostrarTags() {
            tagsDiv.textContent = '';
            tags.forEach(tag => {
                const etiqueta = document.createElement('LI');
                etiqueta.classList.add('formulario__tag');
                etiqueta.textContent = tag;
                etiqueta.ondblclick = eliminarTag;
                tagsDiv.appendChild(etiqueta);
            })

            actualizarInputHidden();
        }

        function eliminarTag(e) {
            e.target.remove();

            tags = tags.filter(tag => tag !== e.target.textContent);

            actualizarInputHidden();
        }

        function actualizarInputHidden() {
            tagsInputHidden.value = tags.toString(); //toString, convierte el arreglo en un STRING

        }
    }
})() // IIFIE